<template>
    <div class="nb-input" :cell-id="ci" style="position: relative;">
      <div :cell-id="ci" style="position: relative;padding:5px;">
        <div :cell-id="ci" :line-id="l_id"
             :id="'code-line-'+ci.toString()+'-'+l_id.toString()"
             class="nb-code_line_label"
             style="position: relative;"
          v-for="(line,l_id) in flat_raw" :key="l_id">
          <input type="checkbox" name="select-line"
                 :value="ci.toString()+'-'+l_id.toString()"
                 :cell-id="ci" :line-id="l_id"
                 onclick="return false;"
                 style="position: relative;vertical-align:middle;margin-right:5px;"

          ></input>
          <div :cell-id="ci" :line-id="l_id"
               style="position: relative; display: inline-block; width:95%;vertical-align:middle;">
            <pre :cell-id="ci" :line-id="l_id"
                 style="padding: 0; overflow: visible;margin-left: 8px; margin-right: 8px;margin-bottom: 0; margin-top: 0;background-color: rgba(0,0,0,0);"
            ><code :cell-id="ci" :line-id="l_id" :data-language="lang"
                    :class="'lang-'+lang+' code-change'"
            >{{escapeHTML(line)}}</code></pre>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
    export default {
      name: "InputDiv",
      props:['ci','raw','cell'],
      data(){
        return{
          lang:"",
          flat_raw:[],
        }
      },
      methods:{
        escapeHTML(raw){
          // #################################################################
          // return raw
          var replaced=raw.replace(/</g,"&lt;").replace(/>/g,"&gt;");
          //console.log("#"+replaced+"#")
          return replaced
        },
      },
      computed: {

      },
      mounted() {

        let notebook=this.cell.worksheet.notebook;
        let m=notebook.metadata;
        this.lang=this.cell.raw.language||m.language||m.kernelspec&&m.kernelspec.language||m.language_info&&m.language_info.name;

        this.flat_raw = this.raw.flat(Infinity)


        //this.st_index = this.total_label + 0
        //this.$store.commit('initLabel',this.flat_raw.length)

        //Prism.highlightAll();
      }
    }
</script>

<style scoped>

  .nb-code_line_label {
    position: relative;
    width:100%;
    background-color: whitesmoke;
  }
  .nb-code_line_label:hover{
    background-color: ivory;

  }
  .code-change{
    background-color: rgba(0,0,0,0);
    white-space: pre-wrap;
    white-space: -moz-pre-wrap;
    white-space: -pre-wrap;
    white-space: -o-pre-wrap;
    word-wrap: break-word;
  }
  div.nb-commenting-label{
    background-color: cornsilk;
  }
  .nb-commented-label{
    background-color: mintcream;
  }
</style>



