<template>
  <div id="content-div" ref="content_div" style="position: relative;"
       @mouseup="onMouseUp">
    <div>contentDiv.vue</div>
    <div id="main"
         v-bind:class="{'un-select':!lock_flag}"
         style="position: relative;"
    >

      <div id="notebook-holder" ref="notebook"
           style="position: relative;"
      >
      </div>
      <div style="height: 100px;"></div>
    </div>
    <el-button id="comment_btn"
               :style="{visibility: comment_btn_vis,top:comment_btn_top}"
               @click="onClickCommentBtn"
    >
      标注
    </el-button>
    <CommentLayout ref="comment_layout"/>
  </div>
</template>

<script>
  import Vue from 'vue';
  import CommentLayout from "./CommentLayout.vue";
  import InputDiv from "./InputDiv";
  import { mapState } from 'vuex'

  let inputRender = function (ci,raw,cell) {

    // 动态创建子组件
    let Construct = Vue.extend(InputDiv)
    let instance = new Construct({
      propsData: { ci: ci, raw:raw, cell:cell }
    })
    instance.$mount() // pass nothing
    return instance.$el

  }


    export default {
      name: "ContentDiv",
      components:{CommentLayout},

      //props: ["note_book"],
      data(){
        return {
          note_book:"",
          //selection:[-1,-1,-1,-1],

          comment_btn_vis:'hidden',
          comment_btn_top:"0px",
        }
      },

      watch: {
        /*
        note_book(newVal, oldVal) {
          // let parsed = JSON.parse(newVal);
          // let $holder = this.$refs.notebook;


          //   var notebook = nb.parse(parsed);
          //   while ($holder.hasChildNodes()) {
          //     $holder.removeChild($holder.lastChild);
          //   }
          //   $holder.appendChild(notebook.render());
          //   Prism.highlightAll();

          this.render()

        },
        */
        blush_ed: {
          handler: 'renderBlush',
          immediate: true
        },

      },
      methods: {

        handleMouseSelect() {

          //let text = window.getSelection().toString()
          //console.log(window.getSelection())
          //console.log(text)
          let selection = window.getSelection()
          let text = selection.toString()

          let st_node = selection.anchorNode
          let ed_node = selection.focusNode

          // console.log(selection)

          let getFamily = function (x_node) {
            let x_family = [x_node];
            for(let i=0;x_family[i];i++)
            {
              if('className' in x_family[i] && x_family[i].className.slice(0,8)==="nb-cell ")
                //if('attributes' in x_family[i] && x_family[i].attributes.getNamedItem('cell-id'))
              {
                // console.log(x_family[i],x_family[i].attributes,x_family[i].attributes.getNamedItem('cell-id'))
                return [true,x_family]
              }
              x_family[i+1]=x_family[i].parentNode
            }
          }
          let [st_flag,st_family] = getFamily(st_node)
          let [ed_flag,ed_family] = getFamily(ed_node)

          let getLine = function(x_node){
            if('attributes' in x_node && x_node.attributes.getNamedItem('line-id'))
            {
              // console.log(x_family[i],x_family[i].attributes,x_family[i].attributes.getNamedItem('cell-id'))
              return x_node.attributes.getNamedItem('line-id').value
            }
            return -1
          }
          let st_line = getLine(st_family[st_family.length-1])
          let ed_line = getLine(ed_family[ed_family.length-1])

          console.log('st:',st_cell,st_line,'ed:',ed_cell,ed_line)



        },

        renderBlush(){
          //console.log(this.blush_st,this.blush_ed,this.raw_list)
          if(!this.blush_st||!this.blush_ed)
            return
          let st = Math.min(this.blush_st._index,this.blush_ed._index)
          let ed = this.blush_st._index+this.blush_ed._index-st
          for(let i=0;i<st;i++)
            this.label_list[i].firstChild.checked = this.raw_list[i]
          for(let i=st;i<=ed;i++)
            this.label_list[i].firstChild.checked = !this.raw_list[i]
          for(let i=ed+1;i<this.raw_list.length;i++)
            this.label_list[i].firstChild.checked = this.raw_list[i]
          if(this.commenting_index!==-1)
          {
            this.$store.commit('setCommentingColor')
          }
          /*
          let flag=false
          for(let i=0;i<this.raw_list.length;i++)
          {
            if(!flag&&(this.label_list[i]===this.blush_st||this.label_list[i]===this.blush_ed))
            {
              flag = true
              this.label_list[i].firstChild.checked = !this.raw_list[i]
              if(this.blush_st===this.blush_ed)
              {
                flag = false
              }
              continue;
            }
            if(flag&&(this.label_list[i]===this.blush_st||this.label_list[i]===this.blush_ed))
            {
              flag = false
              this.label_list[i].firstChild.checked = !this.raw_list[i]
              continue
            }
            if(flag)
            {
              this.label_list[i].firstChild.checked = !this.raw_list[i]
            }
            else
            {
              this.label_list[i].firstChild.checked = this.raw_list[i]
            }
          }
          */
        },
        onBlush(e){
          e.stopPropagation()
          if(this.blush_flag)
          {
            //this.blush_ed = e.currentTarget
            this.$store.commit('setBlushEd',e.currentTarget)
            //console.log(this.blush_ed)
          }

        },
        onMouseDown(e){
          if(this.lock_flag)
            return;

          //this.blush_flag=true;
          this.$store.commit('setBlushFlag',true)
          for(let i=0;i<this.label_list.length;i++)
          {
            this.raw_list[i] = this.label_list[i].firstChild.checked
          }
          this.$store.commit('setBlushSt',e.currentTarget)
          this.$store.commit('setBlushEd',e.currentTarget)
          //this.blush_st = this.blush_ed = e.currentTarget
        },
        onMouseUp(e){
          if(this.blush_flag)
          {
            if(this.commenting_index===-1)
              this.showCommentBtn()
            //this.blush_flag=false;
            this.$store.commit('setBlushFlag',false)
            //this.blush_st = this.blush_ed = null
            this.$store.commit('setBlushSt',null)
            this.$store.commit('setBlushEd',null)
            //console.log(this.label_list);
          }
        },

        showCommentBtn(){
          this.$store.commit('checkComment')
          if(this.comment_st!==-1)
          {
            this.comment_btn_vis = "visible";
            this.comment_btn_top = this.height_list[this.blush_ed._index].toString()+'px'
          }
          else
          {
            this.comment_btn_vis = "hidden";
          }
        },
        onClickCommentBtn(){
          //window.scrollTo(0,this.height_list[this.comment_st]-200)
          //console.log(window.screenTop)
          this.$store.commit('setLockFlag')
          this.$store.commit('setNewingFlag')
          this.$refs.comment_layout.addComment(this.height_list[this.comment_st])
          this.comment_btn_vis = "hidden";
        },

        getElementTop(element) {
          //let cnt = []
          let actualTop = element.offsetTop;    //这是获取元素距父元素顶部的距离
          //cnt.push([actualTop,element])
          let current = element.offsetParent;   //这是获取父元素
          while (current.id !== "content-div") {      //当它上面有元素时就继续执行
            actualTop += current.offsetTop;   //这是获取父元素距它的父元素顶部的距离累加起来
            //cnt.push([current.offsetTop,current])
            current = current.offsetParent;　　//继续找父元素
          }
          //console.log(cnt)
          return actualTop;
        },
        getHeightList(){
          let height_list = []
          for(let i=0;i<this.label_list.length;i++)
          {
            height_list.push(this.getElementTop(this.label_list[i]))
            //this.height_list.push(this.getCoords(this.label_list[i]))
          }
          this.$store.commit('initHeightList',height_list)
          //console.log("AAAA",this.height_list,_)

          /*
          // 动态创建子组件
          let Construct = Vue.extend(CommentLayout)
          let instance = new Construct({
            propsData: { height_list: this.height_list }
          })
          instance.$mount() // pass nothing
          let $content_div = this.$refs.content_div
          $content_div.appendChild(instance.$el)
          console.log([$content_div])
          */
        },
        async render() {
          if (this.note_book === '') {
            return
          }
          let parsed = JSON.parse(this.note_book);

          let $holder = this.$refs.notebook;

          nb.setInputRender(inputRender)
          var notebook = nb.parse(parsed);
          while ($holder.hasChildNodes()) {
            $holder.removeChild($holder.lastChild);
          }
          /*
          let $content = this.$refs.content_div
          while ($content.lastChild.className==='comment-layout')
          {
            $content.removeChild($content.lastChild);
          }
          */
          let _ = await notebook.render()
          $holder.appendChild(_);

          Prism.highlightAll();

          /*
          $holder.childNodes.forEach((nb)=>{
            nb.childNodes.forEach((workSheet)=>{
              workSheet.childNodes.forEach((cell)=>{
                //console.log(cell)
                if(cell.className==="nb-cell nb-code-cell")
                {
                  //code cell
                  cell.childNodes.forEach((iop)=>{
                    if(iop.className==="nb-input")
                    {
                      //nb-input
                      iop.childNodes[0].childNodes.forEach((label)=>{
                        //console.log(label)
                        if(label.nodeName!=="#text")
                        {
                          label.addEventListener('mouseover', this.blush, false)
                          label.addEventListener('click', this.click, false)
                        }

                      })
                    }
                    else
                    {

                    }
                  })
                }
                else
                {

                }
              })
            })
          })
          */
          this.$store.commit('initLabelList',document.querySelectorAll(".nb-code_line_label"))
          this.label_list.forEach((l,i)=>
          {
            l.addEventListener('mouseover', this.onBlush, false)
            l.addEventListener('mousedown', this.onMouseDown, false)
            l._index = i;
          })
          //this.raw_list = new Array(this.label_list.length).fill(false)
          this.$store.commit('initRawList',new Array(this.label_list.length).fill(false))
          this.getHeightList()

          //console.log(this.label_list)

          /*
          let workSheet = $holder.childNodes[0].childNodes[0];
          for(let i=0;i<workSheet.childNodes.length;i++)
          {
            let cell = workSheet.childNodes[i]
            cell.id = "cell_"+i.toString()
            if(cell.className==="nb-cell nb-code-cell")
            {
              let pre = cell.childNodes[0].childNodes[0]
              let code_text = pre.childNodes[0].innerHTML
              //console.log([pre.childNodes[0]])
              let code_texts = code_text.split('\n');
              pre.removeChild(pre.lastChild)
              for(let t=0;t<code_texts.length;t++)
              {
                let code_line = document.createElement("code")
                code_line.id = cell.id+"-"+t.toString()
                code_line.className = "lang-python"
                code_line.innerHTML=code_texts[t]
                pre.appendChild(code_line)

                let line_break = document.createElement("text")
                line_break.innerHTML = '\n'
                pre.appendChild(line_break)
              }

            }
          }
          */


          //console.log(notebook)

        }

      },
      computed: {
        ...mapState(['lock_flag','commenting_index','comment_list',
          'label_list','height_list','blush_flag','blush_st','blush_ed','raw_list',
          'comment_st','comment_ed']),
      },

      async mounted () {
        const resp = await fetch('/static/SyntheticTest.ipynb')
        const text = await resp.text()
        this.note_book = text
        //console.log(typeof this.note_book)
        this.render()
      },
    }
</script>

<style scoped>

  .un-select{
    moz-user-select: -moz-none;
    -moz-user-select: none;
    -o-user-select:none;
    -khtml-user-select:none;
    -webkit-user-select:none;
    -ms-user-select:none;
    user-select:none;
  }
  #comment_btn{
    position: absolute;
    left: 50%;
    margin-left: 250px;
    z-index: 1;
  }

</style>



