<template>
  <div class="note_book">

    <h2>This is note_book !!!</h2>
    <nbpreview
    :note_book="notebook"

    >
    </nbpreview>
  </div>
</template>

<script>

import nbpreview from './nbpreview.vue'

export default {
  name: "note_book",

  components: {
    nbpreview
  },
  async mounted () {
    const resp = await fetch('/static/SyntheticTest.ipynb')
    const text = await resp.text()
    this.notebook = text
  },

  data() {
    return {
      notebook: '',
      msg: "hahaha, this is notebook page",

    };
  },

  methods: {
  },
  computed: {
    showCurrentNotebook() {
      return this.$store.state.currentNotebook;
    },
  }
};
</script>

<style scoped>
.note_book {
  color: rgb(49, 49, 49);
  /*overflow-y: scroll;*/
  height: 100%;
}
</style>



