<template>
  <div class="bottom_bnt">
    <el-row>
      <el-button type="primary" round @click="submit"> Submit </el-button>
      <el-button type="primary" round @click="getNext"> Next one </el-button>
      <el-button type="primary" round @click="quit"> Quit </el-button>
    </el-row>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: "bottom_bnt",
  data() {
    return {
      // test_data,
    };
  },
  methods: {

    //提交当前注释、刷选信息
    submit() {
      this.$confirm("确定提交当前页面注释吗？", {
        confirmButtonText: "Yes",
        cancelButtonText: "No",
        type: "warning",
      }).then(() => {
        alert("submit success!");
      });
    },

    //获取下一个notebook数据
    getNext() {
      console.log("get next notebook!");

      axios.get('http://localhost:5000/data')
      .then(res => {
        console.log(res)
        // this.test_data = res.data.msgs
        this.$store.commit('getCurrentNotebook', res.data)
      })

      axios.post('http://localhost:5000/data', {
        params: {name: 'this is post'}
      }).then(res => {
        console.log(res)
      }).catch(err => {
        console.log(err)
      })
    },

    //退出
    quit() {
      this.$confirm("确定要退出吗？你已经保存好当前的注释了吗？", {
        confirmButtonText: "Yes",
        cancelButtonText: "No",
        type: "warning",
      }).then(() => {
        axios.get('http://localhost:5000/user/data')
      .then(res => {
        console.log(res)
        // this.test_data = res.data.msgs
        this.$store.commit('getCurrentNotebook', res.data)
      })

        this.$router.push("/login");
      });
    },
  },
};
</script>

<style scoped>
.bottom_bnt {
  position: fixed;
  bottom: 20px;
  left: 40%;
  color: rgb(49, 49, 49);
}
</style>


