<template>




    <div id="commentLayout" ref="comment_layout" class="comment-layout">

      <CommentEl :comment_index="comment_index"
                 :style="{position: 'absolute', top: comment.cur_top.toString()+'px'}"
                 v-for="(comment,comment_index) in comment_list" :key="comment.c_id"
      >
      </CommentEl>
      <!--
      <CommentEl p_id="111" content_str="评论1" time_str="2020-12-25"></CommentEl>
      <div v-bind:style='{position: "absolute", top: comment.height}' v-for="comment in comment_list" :key="comment.c_id">
        {{ comment.content }}
      </div>
      -->

    </div>
</template>

<script>
  import { mapState } from 'vuex'
  import CommentEl from "./CommentEl.vue";
    export default {
        name: "CommentLayout",
      /*
      props:['height_list'],
      */
      components:{CommentEl},
      data(){
          return{
            total_cnt:0,
          }
      },
      methods:{
          click()
          {
            let $comment_layout = this.$refs.comment_layout
            console.log(this.height_list)
          },
          addComment(current_top)
          {
            const comment_el_height = 100;
            //console.log(st,ed,current_top)
            let sort_id =0;
            //console.log("AAAAAAAAA",this.comment_sort,this.comment_list)
            for(;sort_id<this.comment_sort.length;sort_id++)
            {
              if(this.comment_list[this.comment_sort[sort_id]].cur_top>=current_top)
              {
                break;
              }
            }
            current_top = sort_id===0?current_top:Math.max(current_top,this.comment_list[this.comment_sort[sort_id-1]].cur_top+comment_el_height)
            let last_top = current_top
            //console.log("BBBBBBBBBBBBB",sort_id,last_top,current_top)
            for(let i=sort_id;i<this.comment_sort.length;i++)
            {
              //console.log('CCCCCCCCC',sort_id,this.comment_sort[sort_id],this.comment_list[this.comment_sort[sort_id]])
              if(this.comment_list[this.comment_sort[sort_id]].cur_top<last_top+comment_el_height)
              {
                this.$store.commit('adjustCommentTop',{comment_index:this.comment_sort[i],new_top:last_top+comment_el_height})
                last_top = last_top+comment_el_height
              }
              else
              {
                break
              }
            }
            let c_id = this.total_cnt
            this.total_cnt += 1
            let comment_index = this.comment_sort.length
            this.$store.commit('insertComment',
              {
                c_id:c_id,
                u_id:-1,
                nb_id:-1,
                st_id:this.comment_st,
                ed_id:this.comment_ed,
                cur_top:current_top,
                time_str:new Date().toDateString(),
                content_str:""
              }
              )
            this.$store.commit('insertCommentSort',
              {
                comment_index:comment_index,
                rank:sort_id,
              }
            )
            this.$store.commit('setCommentingIndex',comment_index)
            this.$store.commit('setCommentingColor')


          }
      },
      mounted() {
        //console.log(this.height_list)
        /*
        this.height_list.forEach((h)=>{

          this.comment_list.push({
            c_id:h,
            p_id:1,
            time:"1225",
            st:10,
            ed:16,
            content:"评论"+h.toString(),
            height:(h).toString()+"px"
          })
        })
        */
        //console.log(this.height_list)
        //console.log(this.comment_list)
      },
      computed: {
        ...mapState(['commenting_index','comment_list','comment_sort','comment_st','comment_ed']),
      }
    }
</script>

<style scoped>
  #commentLayout{
    position: absolute;
    top: 0;
    left: 50%;
    margin-left: 310px;

    /*width: 500px;*/
    /*background-color: blue;*/

  }
  .comment_div{

  }
</style>



