<template>




    <div>
      <div class="comment-el-div" :style="{backgroundColor:commenting_index===comment_index?'cornsilk':'white'}">
        <div style="height: 30px;margin-top: 5px;">
          <div class="fl clearfix" style="height: 30px;line-height: 30px;margin-left: 5px;">{{u_id}}</div>
          <div class="fl clearfix" style="height: 30px;line-height: 30px;margin-left: 10px;font-size: 10px;">{{time_str}}</div>
          <el-button class="fr clearfix" v-if="comment_index!==commenting_index" @click="onDelete">删除</el-button>
          <el-button class="fr clearfix" v-if="comment_index!==commenting_index" @click="onChange">修改</el-button>
          <el-button class="fr clearfix" v-if="comment_index===commenting_index"
                     @click="onSubmit" style="margin-left: 0;">确定</el-button>
          <el-button class="fr clearfix" v-if="comment_index===commenting_index&&lock_flag"
                     @click="onSelect" style="margin-left: 0;">重选</el-button>
          <el-button class="fr clearfix" v-if="comment_index===commenting_index&&!lock_flag"
                     @click="onLock" style="margin-left: 0;">锁定</el-button>
          <el-button class="fr clearfix" v-if="comment_index===commenting_index"
                     @click="onCancle" style="margin-left: 0;">取消</el-button>
        </div>
        <div style="margin-top: 10px;">
          <el-input v-if="comment_index===commenting_index" v-model="content_str"></el-input>
          <el-popover
            placement="bottom-start"
            width="315"
            trigger="hover"
            :disabled="content_str.length<=20"
            :content="content_str"
            v-if="comment_index!==commenting_index" >
            <div class="inline-text" slot="reference" style="padding: 10px;">{{content_str}}</div>
          </el-popover>
          <!--
          <div class="inline-text" style="padding: 10px;visibility: hidden;" ref="inline_div">{{content_str}}</div>
          -->
          <!--
          <el-tooltip class="item" effect="dark" :content="node.label" placement="top-end"
                      :disabled="node.label.length<22"
                      v-show="!((node_rename.type==data.type)&&(node_rename.id==data.id))">
                            <span class="menuitem_div inline-text">
                                {{node.label}}
                            </span>
          </el-tooltip>
          -->
        </div>
      </div>
    </div>
</template>

<script>
  import { mapState } from 'vuex'
    export default {
        name: "CommentEl",
      props:['comment_index'],
      data(){
          return{
            first_flag:true,

            content_str:"",
            st_id:-1,
            ed_id:-1,
          }
      },
      methods:{
          onSubmit(){
            if(this.lock_flag||this.onLock())
            {
              this.$store.commit('updateComment',
                {comment_index:this.comment_index,
                  content_str:this.content_str,
                  st_id:this.st_id,
                  ed_id:this.ed_id,
                })
              this.$store.commit('cleanCommentingColor')
              this.$store.commit('setCommentedColor',this.comment_index)

              this.$store.commit('cleanNewingFlag')
              this.$store.commit('cleanLockFlag')
              this.$store.commit('cleanChecked')

              this.$store.commit('cleanCommentingIndex')
            }
          },

          onSelect(){
            this.$store.commit('cleanLockFlag')
            /*
            this.$store.commit('deleteComment',this.comment_index)
            this.$store.commit('cleanCommentingIndex')

             */
          },
          onLock(){
            this.$store.commit('checkComment')
            if(this.comment_st!==-1)
            {
              this.st_id = this.comment_st;
              this.ed_id = this.comment_ed;
              this.$store.commit('setLockFlag')
              return true;
            }
            else {
              this.$message.error('所选代码不连续或未选择代码！');
              return false
            }
            /*
            this.$store.commit('deleteComment',this.comment_index)
            this.$store.commit('cleanCommentingIndex')

             */
          },
          onCancle(){
            if(this.newing_flag)
            {
              this.$store.commit('cleanCommentingColor')

              this.$store.commit('cleanNewingFlag')
              this.$store.commit('cleanLockFlag')
              this.$store.commit('cleanChecked')

              this.$store.commit('deleteComment',this.comment_index)

              this.$store.commit('cleanCommentingIndex')
            }
            else
            {
              this.content_str = this.comment_list[this.comment_index].content_str;
              this.st_id = this.comment_list[this.comment_index].st_id;
              this.ed_id = this.comment_list[this.comment_index].ed_id;

              this.$store.commit('cleanCommentingColor')
              this.$store.commit('setCommentedColor',this.comment_index)

              this.$store.commit('cleanLockFlag')
              this.$store.commit('cleanChecked')

              this.$store.commit('cleanCommentingIndex')
            }
          },
          onChange(){
            if(this.commenting_index!==-1)
            {
              this.$message.error('正在修改其他注释');
            }
            else
            {
              this.$store.commit('setCommentingIndex',this.comment_index)

              this.$store.commit('setChecked',this.comment_index)
              this.$store.commit('setLockFlag')

              this.$store.commit('cleanCommentedColor',this.comment_index)
              this.$store.commit('setCommentingColor')
            }
          },
          onDelete(){
            if(this.commenting_index!==-1)
            {
              this.$message.error('正在修改其他注释');
            }
            else
            {
              this.$store.commit('cleanCommentedColor',this.comment_index)

              this.$store.commit('deleteComment',this.comment_index)

            }

          },
      },
      computed: {
        ...mapState(['commenting_index','comment_list','comment_sort',
          'lock_flag','newing_flag',
          'comment_st','comment_ed','label_list']),

        u_id:function(){
          return this.comment_list[this.comment_index].u_id;
        },
        time_str:function(){
          return this.comment_list[this.comment_index].time_str;
        },
        /*
        not_ellipsis_flag:function(){
          let $inline_div = this.$refs.inline_div
          console.log($inline_div.clientWidth,$inline_div.scrollWidth)
          if($inline_div.clientWidth<$inline_div.scrollWidth){
            return false;
          }
          return true;
        },
        */
      },
      mounted() {
        this.content_str = this.comment_list[this.comment_index].content_str;
        this.st_id = this.comment_list[this.comment_index].st_id;
        this.ed_id = this.comment_list[this.comment_index].ed_id;
      }
    }
</script>

<style scoped>
  .comment-el-div{
    position: relative;
    border: 1px solid lightgray;
    border-radius: 5px;
    padding: 5px;
    width: 350px;
    height: 80px;
  }
</style>



