<template>
  <div class="header">
    <div class="logo">This is Logo</div>
    <div class="header-right header-user-con">
      <!-- 用户头像 -->
      <div class="user-avator">
        <img src="../assets/img/01.jpg" />
      </div>
      <!-- 用户名下拉菜单 -->
      <el-dropdown class="user-name" trigger="click" @command="handleCommand">
        <span class="el-dropdown-link">
          {{ username }}
          <i class="el-icon-caret-bottom"></i>
        </span>
        <el-dropdown-menu slot="dropdown" class="drop_down">
          <el-dropdown-item command="gotoProfile">我的账户</el-dropdown-item>
          <el-dropdown-item divided command="loginout"
            >退出登录</el-dropdown-item
          >
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </div>
</template>
<script>

export default {
  data() {
    return {
      activeIndex: "1",
      activeIndex2: "1",
      collapse: false,
      fullscreen: false,
      name: "ZhangJing",
      message: 2,
      items: [
        {
          index: "dashboard",
          title: "首页",
        },
        {
          index: "class",
          title: "我的班级",
        },
        {
          index: "course",
          title: "我的课程",
        },
        {
          index: "project",
          title: "学习项目",
        },
        {
          index: "task",
          title: "学习任务",
        },
        {
          index: "resource",
          title: "课程资源",
          subs: [
            {
              index: "question",
              title: "题库管理",
            },
            {
              index: "dco",
              title: "我的文档",
            },
            {
              index: "media",
              title: "我的音视频",
            },
            {
              index: "template",
              title: "模板管理",
            },
          ],
        },
        {
          index: "upload",
          title: "学习圈",
        },
      ],
    };
  },
  computed: {
    // handleSelect(key, keyPath) {
    //   console.log(key, keyPath);
    // },
    username() {
      //   let username = localStorage.getItem("ms_username");
      return this.name;
    },
    // onRoutes() {
    //   return this.$route.path.replace("/", "");
    // },
  },
  methods: {
    // 用户名下拉菜单选择事件
    handleCommand(command) {
      if (command === "loginout") {
        localStorage.removeItem("ms_username");
        this.$router.push("/login");
      }
      //   else if (command === "gotoProfile") {
      //     console.log("gotoProfile");
      //   }
    },
    // 全屏事件
    handleFullScreen() {
      let element = document.documentElement;
      if (this.fullscreen) {
        if (document.exitFullscreen) {
          document.exitFullscreen();
        } else if (document.webkitCancelFullScreen) {
          document.webkitCancelFullScreen();
        } else if (document.mozCancelFullScreen) {
          document.mozCancelFullScreen();
        } else if (document.msExitFullscreen) {
          document.msExitFullscreen();
        }
      } else {
        if (element.requestFullscreen) {
          element.requestFullscreen();
        } else if (element.webkitRequestFullScreen) {
          element.webkitRequestFullScreen();
        } else if (element.mozRequestFullScreen) {
          element.mozRequestFullScreen();
        } else if (element.msRequestFullscreen) {
          // IE11
          element.msRequestFullscreen();
        }
      }
      this.fullscreen = !this.fullscreen;
    },
  },
  mounted() {
    // if (document.body.clientWidth < 1500) {
    //   this.collapseChage()
    // }
  },
};
</script>

<style scoped>
.header {
  position: relative;
  box-sizing: border-box;
  width: 100%;
  height: 70px;
  font-size: 22px;
  color: rgb(58, 45, 45);
  background-color: rgba(255,255,255,0.5)
}
.header .logo {
  float: left;
  width: 20%;
  line-height: 70px;
  padding-left: 60px;
}
.header-right {
  float: right;
  min-width: 220px;
  padding-right: 30px;
}
.header-user-con {
  display: flex;
  height: 70px;
  align-items: center;
}

.user-name {
  margin-left: 20px;
  /* float: right; */
  /* width: 30px; */
}
.user-avator {
  margin-right: 20px;  
}
.user-avator img {
  display: block;
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.el-dropdown-link {
  color: #fff;
  cursor: pointer;
}
.el-dropdown-menu__item {
  text-align: center;
}
</style>



